import Axios from "@/utils/request"  // 导入配置好的axios文件
import qs from  "qs"

export function queryBankRecordPageList(datas) {
  return Axios({
    url: "/bankRecord/queryBankRecordPageList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}

export function matchPayLog(datas) {
  return Axios({
    url: "/bankRecord/matchPayLog",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}






