<template>
  <div id="app" >
    <div style="width: 260px; position: absolute; right: 0px;top:0px; border: 1px dashed rgb(217, 217, 217)">
      <div style="font-size: 14px; color: #409eff">说明: </div>
      <div style="margin-top: 10px; font-size: 12px">1. 工单是指商户群商户发出的查单，补单指令;</div>
      <div style="margin-top: 10px; font-size: 12px">2. 机器人露娜会收集所有商户群的工单信息;</div>
      <div style="margin-top: 10px; font-size: 12px">3. 红色数字代表未处理的工单信息数量,即对应的代收订单还未达成完成状态;</div>
      <div style="margin-top: 10px; font-size: 12px">4. 工单指令信息显示绿色代表完成，红色未完成;</div>
      <div style="margin-top: 10px; font-size: 12px">5. 选择对应商户群，输入回复消息等同于在飞机群回复</div>
      <div style="margin-top: 10px; font-size: 12px">6. 点击对应工单，输入回复消息等同于在飞机群引用工单信息回复<span style="color: red">(注意：引用回复时，聊天框里的回复前缀不能删除)</span>；</div>
      <div style="margin-top: 10px; font-size: 12px">7. 点击右侧刷新按钮可更新实时工单信息及状态</div>
    </div>
    <div style="height: 100px;width: 100px; position: absolute; right: 0px;top:75%; text-align: right">
      <el-button type="primary" @click="loadData">刷新</el-button>
    </div>
    <JwChat-index :config="config" :showRightBox='false' scrollType="scroll"
                  :taleList="taleList" @enter="bindEnter" @clickTalk="talkEvent"
                  v-model="inputMsg" :toolConfig="tool" :winBarConfig="winBarConfig" @click="testdemo">
    </JwChat-index>
  </div>
</template>
<script>
  import {getUndealOrder, getOrderDetailByGroupId, replyOrder, replyImgOrder, getFilePath } from "../../api/workorder";
  import {deleteCommand} from "../../api/massMsg";

  export default {
    name: 'workorderChat',
    mounted() {
      console.log("demo1", document.getElementById('demo1'))
    },
    created() {
      this.getChatList()
    },
    data() {
      return {
        current: 0,
        replyObj: {
          'groupId': '',
          'content': '',
          'messageId': '',
          'orderText': '',
          'workOrderId': ''
        },
        inputMsg: '',
        //对话
        taleList: [
        ],
        tool: {
          show: ['file'],
          showEmoji: false,
          callback: this.toolEvent
        },
        config: {
          img: require('@/assets/merchant.jpeg'),
          name: '暂无',
          // dept: '最简单、最便捷',
          callback: this.bindCover,
          historyConfig: {
            show: true,
            tip: '加载更多',
            callback: this.bindLoadHistory,
          },
          quickList: [
            // {text: '这里是jwchat，您想了解什么问题。'},
            // {text: 'jwchat是最好的聊天组件'},
            // {text: '谁将烟焚散，散了纵横的牵绊；听弦断，断那三千痴缠。'},
            // {text: '长夏逝去。山野间的初秋悄然涉足。'},
            // {text: '江南风骨，天水成碧，天教心愿与身违。'},
            // {text: '总在不经意的年生。回首彼岸。纵然发现光景绵长。'},
            // {text: '外面的烟花奋力的燃着，屋里的人激情的说着情话'},
            // {text: '假如你是云，我就是雨，一生相伴，风风雨雨；'},
            // {text: '即使泪水在眼中打转，我依旧可以笑的很美，这是你学不来的坚强。'},
            // {text: ' 因为不知来生来世会不会遇到你，所以今生今世我会加倍爱你。'},
          ]
        },
        winBarConfig: {
          active: '',
          width: '240px',
          listHeight: '60px',
          height: '569px',
          list: [],
          callback: this.bindWinBar,

        },
        rightConfig: {
          listTip: '国家选择',
          notice: '选择对应国家 筛选展示对应的telegram商户群',
          list: [
            {
              name: '印度',
            },
            {
              name: '哥伦比亚',
            },
            {
              name: '墨西哥',
            }
          ]
        }
      }
    },
    methods: {
      loadData () {
        this.getChatList()
        this.getChatDetails(this.winBarConfig.active)
      },
      //获取聊天列表集合
      getChatList() {
        let that = this
        getUndealOrder({}
        ).then(function (response) {
          if (response.data.code === 0) {
            let resultList = response.data.data
            resultList.forEach((item, index,) => {
              item.img = require('@/assets/merchant.jpeg')
            })
            that.winBarConfig.list = resultList
            if (that.winBarConfig.list.length > 0) {
              that.winBarConfig.active = that.winBarConfig.list[0].id
            }
          }
        })
      },
      rightClick(type) {
        console.log('rigth', type)
      },
      testdemo() {
        alert('test demo')
      },
      talkEvent(obj) {
        console.log("obj", obj)
      },
      bindEnter() {
        const msg = this.inputMsg
        if (!msg) return;
        const msgObj = {
          "date": this.dateFormate(),
          "text": {"text": msg},
          "mine": true,
          "name": "TMPay",
          "img": require('@/assets/luna.jpg')
        }
        this.replyObj.content = msg
        let index = msg.indexOf('^');
        if (index > 0) {
          //引用回复
          let quote = msg.substr(0, index)
          let realReply = msg.substr(index + 1)
          msgObj.text.text = realReply
          msgObj.name = quote
          this.replyObj.content = realReply
        } else {
          this.replyObj.groupId = this.winBarConfig.active
          this.replyObj.messageId = ''
          this.replyObj.orderText = ''
          this.replyObj.workOrderId = ''
        }
        this.taleList.push(msgObj)
        //telegram 发送 存入信息
        let that = this
        replyOrder(this.replyObj).then(function (response) {
          if (response.data.code === 0) {
            that.$message({message: '发送成功', type: 'success'})
          } else {
            that.$message.error('发送失败');
          }
        })
      },
      /**
       * @description:
       * @param {*} type 当前点击的按钮
       * @param {*} plyload 附加文件或者需要处理的数据
       * @return {*}
       */
      toolEvent(type, plyload) {
        console.log('tools', type, plyload)
        if ('file' == type && plyload.length != 1) {
          this.$message.warning('只能选择一张图片');
          return
        }
        let that = this
        this.$confirm('回复此图片, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          const formData = new FormData()
          formData.append('file', plyload[0], plyload[0].name)
          formData.append('groupId', this.winBarConfig.active)
          replyImgOrder(formData).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '发送成功',
                type: 'success'
              })
              // 记载图片
              const msgObj = {
                "date": that.dateFormate(),
                "text": {"text": ''},
                "mine": true,
                "name": "TMPay",
                "img": require('@/assets/luna.jpg')
              }
              let imgSrc = 'http://13.200.37.118:8080/manage/workOrder/getReplyImg?id=' + response.data.data
              msgObj.text.text = '<img data-src='+ imgSrc + '>'
              that.taleList.push(msgObj)
            } else {
              that.$message.error('发送失败');
            }
          })
        })


      },
      /**
       * @description: 点击加载更多的回调函数
       * @param {*}
       * @return {*}
       */
      bindLoadHistory() {
        const history = new Array(3).fill().map((i, j) => {
          return {
            "date": "2020/05/20 23:19:07",
            "text": {"text": j + new Date()},
            "mine": false,
            "name": "JwChat",
            "img": "image/three.jpeg"
          }
        })
        let list = history.concat(this.list)
        this.list = list
        console.log('加载历史', list, history)
      },
      bindCover(type) {
        console.log('header', type)
      },
      bindWinBar(play = {}) {
        const {type, data = {}} = play
        console.log(data);
        if (type === 'winBar') {
          const {id, dept, name, img} = data
          this.winBarConfig.active = id
          this.config.name = name
          //请求对应的对话列表
          this.getChatDetails(id)
          this.replyObj = {}
        }
      },
      //获取工单详细信息
      getChatDetails(groupId) {
        this.current = 0
        let that = this
        getOrderDetailByGroupId({groupId: groupId}).then(function (response) {
          if (response.data.code === 0) {
            let resultList = response.data.data
            that.taleList = []
            resultList.forEach((item, index,) => {
              var obj = new Object()
              obj.workOrderId = item.orderId
              obj.id = item.messageId
              obj.groupId = item.groupId
              obj.orderText = item.orderText
              obj.reply = item.reply
              obj.text = {text: ''}
              //自己的回复
              if (item.mine == '1') {
                obj.mine = true
                if (item.orderText) {
                  obj.date = '回复:' + item.orderText  + ' -- ' + item.createTime
                }else {
                  obj.date = item.nickname  + ' -- ' + item.createTime
                }
                obj.img = require('@/assets/luna.jpg')
                if ('reply_img' == item.orderId) {
                  //图片回复
                  let imgSrc = 'http://13.200.37.118:8080/manage/workOrder/getReplyImg?id=' + item.reply
                  obj.text.text = '<img data-src='+ imgSrc + '>'
                } else {
                  obj.text.text = '<p>' + item.reply + '</p>'
                }
              } else {
                obj.mine = false
                obj.date = item.nickname + ' -- ' + item.createTime
                obj.img = require('@/assets/merchant.jpeg')
                if ('chat' == item.orderId) {
                  //闲聊消息
                  if ('1' == item.orderText) {
                    //文本
                    obj.text.text = '<p id=' + item.messageId + ' style="cursor:default;">' + item.reply + '</p>'
                  }
                  if ('2' == item.orderText) {
                    //图片
                    obj.text.text = '<p id=' + item.messageId + ' style="cursor:pointer;color:#409eff">图片(点击查看)</p>'
                  }
                } else {
                  //客户工单消息
                  if (item.status != '2') {
                    obj.text.text = '<p id=' + item.messageId + ' class="undelOrder" style="cursor: pointer;color: red">' + item.orderText + '</p>'
                  } else {
                    obj.text.text = '<p id=' + item.messageId + ' style="cursor: pointer;color: green">' + item.orderText + '</p>'
                  }
                }
              }
              that.taleList.push(obj)
            })
            that.$nextTick(() => {
              that.taleList.forEach((item, index,) => {
                if (!item.mine && item.workOrderId != 'chat') {
                  let id = item.id
                  document.getElementById(id).onclick = function () {
                    that.queryReplyDetail(item)
                  }
                }
                //图片查看事件绑定
                if (!item.mine && item.workOrderId == 'chat' && item.orderText == '2') {
                  let id = item.id
                  document.getElementById(id).onclick = function () {
                    that.loadImg(item)
                  }
                }
              })
              //append 上定位元素dom
              var newUp = document.createElement('div')
              newUp.id = 'newUpId'
              newUp.className = 'upBtn'
              newUp.innerText = ''
              newUp.onclick = function () {
                var undelOrder = document.getElementsByClassName("undelOrder")
                var len = undelOrder.length
                var ty = 0
                if (that.current > 0) {
                  ty = undelOrder[that.current].parentNode.parentNode.parentNode.parentNode.offsetTop
                  that.current--
                } else {
                  that.current = 0
                  ty = undelOrder[0].parentNode.parentNode.parentNode.parentNode.offsetTop
                }
                var web__main = document.getElementsByClassName("web__main");
                web__main[0].style.transform = "translateX(0px) translateY(-"+ ty +"px) translateZ(1px)"
              }
              var newDown = document.createElement('div')
              newDown.id = 'newDownId'
              newDown.className = 'downBtn2'
              newDown.innerText = ''
              newDown.onclick = function () {
                var undelOrder = document.getElementsByClassName("undelOrder")
                var len = undelOrder.length
                var ty = 0
                if (that.current < len) {
                  ty = undelOrder[that.current].parentNode.parentNode.parentNode.parentNode.offsetTop
                  that.current++
                } else {
                  that.current = 0
                  ty = undelOrder[0].parentNode.parentNode.parentNode.parentNode.offsetTop
                }
                var web__main = document.getElementsByClassName("web__main");
                web__main[0].style.transform = "translateX(0px) translateY(-"+ ty +"px) translateZ(1px)"
              }
              var wrapper = document.getElementsByClassName('wrapper')
              wrapper[0].appendChild(newUp)
              wrapper[0].appendChild(newDown)
            });
          }
        })
      },
      queryReplyDetail(item) {
        console.log('queryReplyDetail:', item)
        this.inputMsg = '回复：' + item.orderText + "^"
        this.replyObj.groupId = item.groupId
        this.replyObj.messageId = item.id
        this.replyObj.orderText = item.orderText
        this.replyObj.workOrderId = item.workOrderId
      },
      loadImg(item) {
        let that = this
        getFilePath(item.reply).then(function (response) {
          console.log(response)
          let rsp = response.data
          if (rsp.ok) {
            let imgAd = 'https://api.telegram.org/file/bot5302362125:AAE7TkE4Yr-cDJBI004OMviQvTU0y71NNhM/' + rsp.result.file_path
            that.taleList.forEach((obj, index,) => {
              if (obj.id == item.id) {
                obj.text.text = '<img data-src='+ imgAd + '>'
              }
            })
          } else {
            that.$message.error('加载图片失败');
          }
        })
      },
      dateFormate() {
        var date = new Date()
        var Y = date.getFullYear();
        var M = date.getMonth() + 1;
        M = M < 10 ? '0' + M : M;// 不够两位补充0
        var D = date.getDate();
        D = D < 10 ? '0' + D : D;
        var H = date.getHours();
        H = H < 10 ? '0' + H : H;
        var Mi = date.getMinutes();
        Mi = Mi < 10 ? '0' + Mi : Mi;
        var S = date.getSeconds();
        S = S < 10 ? '0' + S : S;
        return Y + '-' + M + '-' + D + ' ' + H + ':' + Mi + ':' + S;
      },
    }
  }
</script>
<style scoped>
  >>> .winBar {
    border: 1px solid #dcdfe6 !important;
    height: 568px !important;
  }

  >>> .winBarBox {
    margin-top: 0px !important;
  }

  >>> .ChatPage {
    border-bottom: 0.5px solid #dcdfe6 !important;
  }

  >>> .web__main-text > .web__msg--img {
    background-color: white;
  }

  >>> .upBtn {
    position: absolute;
    cursor: pointer;
    right: 1rem;
    width: 2rem;
    height: 2rem;
    bottom: 7rem;
  }

  >>> .upBtn:before {
    content: "⬆️";
    position: absolute;
    background: hsla(0,0%,80%,.2);
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
    text-align: center;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
  }

  >>> .downBtn2 {
    position: absolute;
    cursor: pointer;
    right: 1rem;
    width: 2rem;
    height: 2rem;
    bottom: 4rem;
  }

  >>> .downBtn2:before {
    content: "⬇️";
    position: absolute;
    background: hsla(0,0%,80%,.2);
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
    text-align: center;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
  }


</style>



